import wxShare from '../utils/wxshare';
export default {
  name: 'Process',
  data () {
    return {
      step: this.$route.query.step,
    }
  },
  created() {
    wxShare({ title: '444', desc: '555', link: 'http://working.rzzc.ltd/?wxid=1', logo: ''});
    this.getWxShare();
  },
  watch: {
    '$route' (to, from) {
      this.step = this.$route.query.step;
    }
  },
  methods:{
    jumpFun(){
      window.location.href = 'http://www.baidu.com';
    },
    //获取A域名
    getWxShare(){
      let self = this;
      self.$http.post(global.url.chatGetTitle,{}).then(res => {
        if(res){
          self.$vux.loading.hide()
          this.title = res.title;
          this.enrollment = res.enrollment;
          this.invitor = res.invitor;
        }
      });
    }
  },
  mounted(){

    let self = this,
      _url = window.location.href;
    //监听返回
    pushHistory();
    window.addEventListener("popstate", function(e) {
      self.jumpFun();
    }, false);

    function pushHistory() {
      var state = {
        title: "title",
        url: _url
      };
      window.history.pushState(state, "title", _url);
    }


    //禁止右键
    window.document.oncontextmenu = function (e) {
      e.preventDefault();
    };

    //禁止下拉
    var startX = 0, startY = 0;
    function touchStart(e) {
      try {
        var touch = e.touches[0],
          x = Number(touch.pageX),
          y = Number(touch.pageY);
        startX = x;
        startY = y;
      } catch (e) {
        alert(e);
      }
    }

    document.addEventListener('touchstart', touchStart);
    var ele = document.getElementById('Process');
    ele.ontouchmove = function (e) {
      var point = e.touches[0],
        eleTop = ele.scrollTop,
        eleScrollHeight = ele.scrollHeight,
        eleOffsetHeight = ele.offsetHeight,
        eleTouchBottom = eleScrollHeight - eleOffsetHeight;
      if (eleTop === 0) {
        if (point.clientY > startY) {
          e.preventDefault();
        }
      }
      else if (eleTop === eleTouchBottom) {
        if (point.clientY < startY) {
          e.preventDefault()
        }
      }
    };

    var _h = document.documentElement.clientHeight;
    document.getElementById('Process').style.height = _h + 'px';

  }
}



