
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XButton
},
props: {
invitor: String,
required: true
},
data () {
return {
wxid : this.$route.query.wxid
}
},
methods:{
show(){
this.$router.push({path: '/show',query :{wxid: this.wxid, invitor:this.invitor}});
}
}
}



